<template>
    <div class="layer">
        <div class="type-title">
            {{ channelName }}
        </div>
        <Loading v-if="isLoading"></Loading>
        <NewsList v-if="!isLoading" :news="news"></NewsList>
        <Pager
            :page="page"
            :limit="limit"
            :total="total"
            :panelNumber="panelNumber"
            @pageChange="handleChange"
        ></Pager>
    </div>
</template>

<script>
import Pager from "../components/Pager";
import NewsList from "../components/news/NewsList";
import * as newServe from "../services/newsService";
import Loading from "../components/Loading";
export default {
    components: {
        Pager,
        NewsList,
        Loading,
    },
    data() {
        return {
            channelName: "",
            page: 3,
            limit: 5,
            total: 0,
            panelNumber: 10,
            news: [],
            isLoading: true,
        };
    },
    methods: {
        // 设置频道名称
        async setChannelName() {
            var channels = await newServe.getNewsChannels();
            var channel = channels.find(
                (item) => item.channelId === this.$route.params.id
            );
            this.channelName = channel.name;
        },
        // 设置所有新闻相关数据
        async setDates() {
            this.isLoading = true;
            var resp = await newServe.getNews(
                this.$route.params.id,
                this.page,
                this.limit
            );
            this.total = resp.allNum;
            this.news = resp.contentlist;
            this.isLoading = false;
        },
        handleChange(newPage) {
            this.page = newPage;
            this.setDates();
        },
    },
    watch: {
        // 监控某些数据的变化，当数据变化时要做一些事儿
        "$route.params.id": {
            immediate: true, //一开始的数据也要当做是一种变化
            handler() {
                this.page= 1;
                this.setChannelName();
                this.setDates();
            },
        },
    },
};
</script>

<style lang='less' scoped>
.type-title {
    margin-top: 15px;
    border-bottom: 1px solid #ddd;
    height: 50px;
    line-height: 50px;
    font-size: 25px;
    width: 100%;
    font-weight: bold;
    color: #666;
    // background-color: #bfa;
}
</style>