<template>
    <div class="pager layer" v-if="total > 0">
        <a
            href=""
            class="page-item"
            :class="{
                disabled: page === 1,
            }"
            @click.prevent="handleChange(1)"
            >首页</a
        >
        <a
            href=""
            class="page-item"
            :class="{
                disabled: page === 1,
            }"
            @click.prevent="handleChange(page - 1)"
            >上一页</a
        >

        <a
            href=""
            v-for="n in numbers"
            :key="n"
            @click.prevent="handleChange(n)"
            :class="{
                active: n===page
            }"
            >{{ n }}</a
        >

        <a
            href=""
            class="page-item"
            :class="{
                disabled: page === pageNumber,
            }"
            @click.prevent="handleChange(page + 1)"
            >下一页</a
        >
        <a
            href=""
            class="page-item"
            :class="{
                disabled: page === pageNumber,
            }"
            @click.prevent="handleChange(pageNumber)"
            >尾页</a
        >
        <span>
            <i>{{ page }}</i>
            /
            <i>{{ pageNumber }}</i>
        </span>
    </div>
</template>

<script>
export default {
    props: {
        page: {
            // 当前页码
            default: 1,
            type: Number,
        },
        total: {
            // 总的数据量
            default: 0,
            type: Number,
        },
        limit: {
            // 页容量：每页显示多少条数据
            default: 10,
            type: Number,
        },
        panelNumber: {
            // 最多显示多少个数字页码
            default: 10,
            type: Number,
        },
    },
    computed: {
        // 总页数
        pageNumber() {
            return Math.ceil(this.total / this.limit);
        },
        minNumber(){
            // 计算当前显示的最小页码数字
            var n = this.page - this.panelNumber/2;
            if(n<1){
                n = 1;
            }
            return n
        },
        maxNumber(){
            // 计算当前显示的最小页码数字
            var n = this.minNumber + this.panelNumber-1;
            if(n>this.pageNumber){
                n = this.pageNumber;
            }
            return n
        },
        numbers() {
            var nums = [];
            for(var i= this.minNumber; i<=this.maxNumber; i++){
                nums.push(i);
            }
            return nums;
        },
    },
    methods: {
        handleChange(newPage) {
            if (newPage < 1) {
                newPage = 1;
            } else if (newPage > this.pageNumber) {
                newPage = this.pageNumber;
            }
            if (newPage === this.page) {
                return;
            }
            this.$emit("pageChange", newPage);
        },
    },
};
</script>

<style lang='less' scoped>
.pager {
    margin: 20px 0 30px 0;
    height: 60px;
    // background-color: #bfa;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #999;
    a {
        display: inline-block;
        width: 40px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        border: 1px solid #999;
        border-radius: 5px;
        color: rgb(0, 102, 255);
        font-size: 15px;
        &:hover{
            background-color: rgb(0, 102, 255);
            color: #fff;
        }
    }
    .disabled {
        color: #999;
        cursor: text;
        &:hover{
            background-color: #ddd;
            color: #000;
        }
    }
    .page-item {
        width: 70px;
        height: 30px;
    }
    .active{
        background-color: rgb(0, 102, 255);
        color: #fff;
    }
    span{
        i{
            font-size: 17px;
        }
    }
}
</style>